import Vue from 'vue'
import Router from 'vue-router'
const Register = () => import('@/components/register')
const Login = () => import('@/components/login')
const Home = () => import('@/components/home/home')
const Detail = () => import('@/components/home/detail')
const Buy = () => import('@/components/buy/buy')
const stepFirst = () => import('@/components/buy/stepFirst')
const stepSecond = () => import('@/components/buy/stepSecond')
const Sell = () => import('@/components/sell/sell')
const My = () => import('@/components/my/my')
const Set = () => import('@/components/my/set')
const ChangePwd = () => import('@/components/my/changepwd')
const Findpwd = () => import('@/components/findpwd')
const PhoneLogin = () => import('@/components/phoneLogin')
Vue.use(Router)

export default new Router({
		  routes: [
		  {
		  	path: '/',
		  	redirect: '/login'
		  },
		  {
		  	path: '/home',
		  	component: Home
		  },
		  {
			 path: '/home/detail',
			 component: Detail
		  },
		  {
			  path: '/buy',
			  component: Buy
		  },
		  {
			  path: '/buy/stepFirst',
			  component: stepFirst
		  },
		  {
			  path: '/buy/stepSecond',
			  component: stepSecond
		  },
		  {
			  path: '/sell',
			  component: Sell
		  },
		  {
			  path: '/my',
			  component: My
		  },
		  {
			   path: '/my/set',
			   component: Set
		  },
		  {
			   path: '/my/set/changepwd',
			   component: ChangePwd
		  },
		  {
			  path: '/register',
			  component: Register
		  },
		  {
			  path: '/login',
			  component: Login
		  },
		  {
			  path: '/findpwd',
			  component: Findpwd
		  },
		  {
			  path: '/phoneLogin',
			  component: PhoneLogin
		  }
	    ]
})



