<template>
  <div id="app" :class="{pdbtm:flag}">
     <loading v-if="isLoading"></loading>
     <!-- <keep-alive> -->
       <transition>
         <router-view class="router-view"></router-view>
       </transition>
    <!-- </keep-alive> -->
    <tab v-if="flag"></tab>
  </div>
</template>

<script>
import tab from '@/components/common/tab'
import loading from '@/components/common/loading'
import { mapState } from 'vuex'
export default {
  name: 'App',
  data () {
    return {
      flag: true
    }
  },
  computed: mapState(['isLoading']),
  components: {
    tab,
    loading
  },
  mounted () {
  },
  watch: {
    '$route.path' (newVal, oldVal) {
      if (newVal === '/home' || newVal === '/buy' || newVal === '/my' || newVal === '/sell') {
        this.flag = true
      } else {
        this.flag = false
      }
    }
  }
}
</script>

<style scoped>
#app{
  overflow-x: hidden;
}
.pdbtm{
  padding-bottom: 50px;
}
.v-enter {
  opacity: 0;
  transform: translateX(100%);
}

.v-leave-to {
  opacity: 0;
  transform: translateX(-100%);
  position: absolute;
}

.v-enter-active,
.v-leave-active {
  transition: all 0.2s linear;
}
</style>



