<template>
  <div>
    <nav class="nav fx fx-ac">
      <router-link  class="tab-item fx-1" v-for="(item,index) in tabs" exact :key="index"  :to="item.name">
        <span class="iconfont" :class="item.icon"></span>
        <span class="tab-label">{{item.label}}</span>
      </router-link>
    </nav>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tabs: [
        {name: 'home', icon: 'icon-remen', label: '首页'},
        {name: 'buy', icon: 'icon-fabu', label: '发布'},
        {name: 'sell', icon: 'icon-faxian', label: '查看'},
        {name: 'my', icon: 'icon-gerenzhongxin', label: '个人中心'}
      ]
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../assets/css/mixins.scss';
@import '../../assets/css/color.scss';
 .nav {
    z-index: 3;
    position:fixed;
    bottom:0;
    left:0;
    width: 100%;
    height: 50px;
    padding-top: 10px;
    .tab-item {
      height: 100%;
      text-align: center;
      background: #DDD;
      color:$tabColor;
      box-sizing: border-box;
      span {
        display: block;
        font-size: 14px;
      }
      .iconfont{
          margin-top: 5px;
          font-size: 22px;
      }
    }
    .router-link-active{
      color: $themeColor;
    }
  }
</style>



